import Image from "next/image";
import home from './image/home.jpg';
{ output: 'export' };
export default function Detail() {
  return (
    <div className="bg-gray-100 min-h-screen flex flex-col items-center p-4">
      <header className="bg-green-400 w-full p-4 flex justify-between items-center rounded-lg shadow-md">
        <h1 className="text-white text-lg font-semibold">Clean House</h1>
        <h1 className="text-white text-lg font-semibold">02096611479</h1>
        <h1 className="text-white text-lg font-semibold">Welcome</h1>
        
        <div className="flex flex-col items-center mb-6">
       
          <Image src="/home.jpg" alt="Clean House Logo" width="50" height="50" />
          <h1 className="text-xl font-bold text-purple-700 mt-2">Clean House</h1>
        </div>
        
        <select className="border p-3 rounded-lg shadow-sm mb-6 w-full max-w-20">
          <option value="th">TH</option>
          <option value="en">EN</option>
          <option value="la">LA</option>
        </select>
      </header>

      <div className="text-center my-10">
        <h1 className="text-2xl font-bold text-gray-800">ບໍລິການທຳຄວາມສະອາດ</h1>
      </div>

      <div className="bg-white p-6 rounded-lg shadow-md w-full max-w-2xl">
        <h1 className="text-xl font-bold text-gray-800 mb-4">ທ່ານຕ້ອງການໃຊ້ ສະຖານທີ່ໃດ</h1>

        <select className="border p-3 rounded-lg shadow-sm mb-6 w-full">
          <option value="lo">ຊື່ບ້ານ ເມືອງ ເເຂວງ</option>
        </select>
        
        <p className="text-gray-600 mb-4">ກະລຸນາເລືອກປະເພດໃຫ້ບໍລິການ...</p>
      
        <select className="border p-3 rounded-lg shadow-sm mb-6 w-full">
          <option value="la">ບ້ານ +ຫ້ອງນອນຫນຶ່ງຫ້ອງ</option>
        </select>
       
        <p className="text-gray-600 mb-4">ເເນະນຳ 2 ຊມ - 2ຊມ. 30 ນາທີ</p>
        <ul className="list-disc list-inside text-gray-600 mb-4">
          <li>1:30</li>
        </ul>
        
        <select className="border p-3 rounded-lg shadow-sm mb-6 w-full">
          <option value="2 ຊມ">2 ຊມ</option>
          <option value="3 ຊມ">3 ຊມ</option>
          <option value="4 ຊມ">4 ຊມ</option>
          <option value="5 ຊມ">5 ຊມ</option>
          <option value="6 ຊມ">6 ຊມ</option> 
        </select>
        
        <p className="text-gray-600 mb-4">*ບໍ່ລວມເວລາພັກຂອງພະນັດກງານ</p>
        <p className="text-gray-600 mb-4">ທ່ານຕ້ອງການໃຊ້ບໍລິການນີ້ເມື່ອໃດ</p>

        <input 
          type="date" 
          className="border p-3 rounded-lg shadow-sm mb-6 w-full"
          defaultValue="2025-01-30"
        />
       
        <p className="text-gray-600 mb-4">ຄ່າບໍລິການລວມທັງຫມົດ (ບໍ່ລວມພາສີ)</p>
        
        <input 
          type="number" 
          className="border p-3 rounded-lg shadow-sm mb-6 w-full"
          defaultValue="350000 ກີບ"
          min="0"
          step="1000"
        />
      </div>
    </div>
  );
}
